<template>
  <div>
      <van-nav-bar
        title="教学列表"
        left-text="返回"
        left-arrow
        @click-left="back"
      />
    <div class="all">
      <div>当前选择的班级ID：{{classId}}</div>
      <van-tag plain>新建教学：</van-tag>
      <div>
        <van-form >
          <van-cell-group inset>
            <van-field v-model="add.Location" label="上课地点：" placeholder="请输入名称" />
            <!-- <van-field v-model="add.startweek" label="开始周数：" placeholder="请输入名称" />
            <van-field v-model="add.endweek" label="结束周数：" placeholder="请输入名称" /> -->
            <van-cell title="点击选择开始日期" :value="date" @click="show = true" />
            <van-field v-model="add.note" label="备注：" placeholder="如：每周一" />
            <van-calendar v-model:show="show" @confirm="onConfirm" :min-date="minDate" :max-date="maxDate" position="left" color="#1989fa"/>
            <van-radio-group v-model="timeSelect" direction="horizontal">
              <van-radio name='0'>选择节数</van-radio>
              <van-radio name='1'>选择时间</van-radio>
            </van-radio-group>
            <van-cell>
              <van-radio-group v-model="add.time" v-show="timeSelect==0">
                <van-radio name='1-2节8:05-9:40'>1-2节8:05-9:40</van-radio>
                <van-radio name='3-5节10:00-12:25'>3-5节10:00-12:25</van-radio>
                <van-radio name='6-7节13:30-15:05'>6-7节13:30-15:05</van-radio>
                <van-radio name='8-9节15:15-16:50'>8-9节15:15-16:50</van-radio>
                <van-radio name='10-11节18:30-20:05'>10-11节18:30-20:05</van-radio>
                <van-radio name='10-12节18:30-20:55'>10-12节18:30-20:55</van-radio>
              </van-radio-group>
              <div v-show="timeSelect==1" class="flex">
                <van-datetime-picker
                  v-model="startTime"
                  type="time"
                  title="选择开始时间"
                  :min-hour="0"
                  :max-hour="23"
                  class="left"
                  confirm-button-text=" "
                  cancel-button-text=" "
                />
                <van-datetime-picker
                  v-model="endTime"
                  type="time"
                  title="选择结束时间"
                  :min-hour="0"
                  :max-hour="23"
                  class="right"
                  confirm-button-text=" "
                  cancel-button-text=" "
                />
              </div>
            </van-cell>
          </van-cell-group>
          <img src="../../static/submitButton.png" class="enterButton" @click="addTeach()">
        </van-form>
      </div>
      <hr>
      <!-- 显示当前有的所有课程 -->
      <van-tag plain>教学列表：</van-tag>
      <div style="color:gray;">点击“删除”按钮删除该教学</div>
      <van-list
          v-model:loading="loading"
          :finished="finished"
          finished-text="没有更多了"
          error-text="加载失败"
          @load="onLoad"
      >
        <div v-for="item in teaches.list" :key="item" class="block">
          <van-card class="vancard">
            <template #title>
              <span class="courseText">上课时间：{{item.time}}</span><br>
              <span class="courseText">上课地点：{{item.location}}</span><br>
              <!-- <span class="courseText">所属班级：{{item.classes}}</span><br> -->
            </template>
            <template #footer>
              <img src="../../static/deleteButton.png" class="enterButton" @click="delTeach(item.no)">
            </template>
          </van-card>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import { ref,reactive,onMounted} from 'vue';
import { useRouter } from "vue-router";
import { useRoute } from "vue-router";
import { Toast } from 'vant';
const axios = require('axios');

export default {
  name:'Home',
  setup(){
    const date = ref('');
    const show = ref(false);
    const timeSelect = ref(3);
    const startTime = ref('12:00');
    const endTime = ref('12:00');
    const loading = ref(false);
    const finished = ref(false);
    const router = useRouter();
    const route = useRoute();
    const classId= route.query.classId;
    const teaches=reactive({list:[]});
    const add=ref({
      time:'',
      Location:'',
      classes:parseInt(classId),
      note:'',
    })

    //日期选择
    const formatDate = (date) => `${date.getYear()+1900}-${date.getMonth() + 1}-${date.getDate()}`;
    const onConfirm = (value) => {
      show.value = false;
      date.value = formatDate(value);
    };

    //加载班级列表
    function onLoad(){
      axios({
            method:'get',
            url:'admin/getteachtime',
            params:{
              classes:classId,
            }
        })
        .then(function (response) {
          for (let i = 0; i < response.data.length; i++) {
            teaches.list.push(response.data[i]);
          }
          loading.value = false;
          finished.value = true;
        })
        .catch(function (error) {
            console.log("catch"+error);
            Toast('加载失败，请检查网络');
        });
    }

    function delTeach(teachId){
      axios({
            method:'get',
            url:'admin/deleteteachtime',
            params:{
              no:teachId,
            }
        })
        .then(function (response) {
          if(response.data.statuscode>=1){
            Toast('删除成功');
            router.go(0);
          }else{
            Toast('删除失败');
          }
        })
        .catch(function (error) {
            console.log("catch"+error);
            Toast('删除失败，请检查网络');
        });
    }

    function addTeach(){
      // if (timeSelect.value==3||date.value==''||add.value.Location=='') {
      //   Toast('请填写信息！');
      //   return;
      // }
      
      //任意一项填写了信息都不算空
      if (timeSelect.value==3&&date.value==''&&add.value.Location==''&&add.value.note=='') {
        Toast('请填写信息！');
        return;
      }
      if (timeSelect.value==0) {
        add.value.time=date.value+'起'+'/'+add.value.note+'/'+add.value.time;
      }else if(timeSelect.value==1){
        add.value.time=date.value+'起'+'/'+add.value.note+'/'+startTime.value+"-"+endTime.value;
      }
      //console.log(add.value);
      axios({
          method:'get',
          url:'admin/createteachtime',
          params:add.value,
      })
      .then(function (response) {
        if(response.data.statuscode==1){
          Toast('添加成功');
          router.go(0);
        }else{
          Toast('添加失败');
        }
      })
      .catch(function (error) {
          console.log("catch"+error);
          Toast('添加失败，请检查网络');
      });
    }


    const back = () => history.back();
    return{
      loading,
      finished,
      back,
      onMounted,
      classId,
      onLoad,
      delTeach,
      addTeach,
      add,
      date,
      show,
      onConfirm,
      formatDate,
      teaches,
      minDate: new Date(2022, 3, 1),
      maxDate: new Date(2025, 3, 1),
      timeSelect,
      startTime,
      endTime,
    };
  },
}
</script>

<style scoped>
  .all {
    margin-left: 15%;
    margin-right: 15%;
    margin-top: 20px;
  }
  .block{
    height: 100%;
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 10px;
    background-color: white;
    border-radius: 20px;
  }
  .vancard{
    margin:20px;
    background-color: white;
  }
  .enterButton{
    height:50px;
    width:auto;
  }
  .font{
    font-size: 20px;
    word-break: keep-all;
  }
  .flex{
    display: flex;
    height: 100%;
    margin: 2%;
    background-color: white;
    border-radius: 5px;
  }
  .flex .left{
    flex:1;
  }
  .flex .right{
    flex:1;
  }
  .courseText{
    font-size: 18px;
  }
</style>